"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/app/auth-context"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { ArrowLeft, Wrench, Building2, Package, Star, Search, ChevronLeft, ChevronRight } from "lucide-react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface Feedback {
  form_id: number
  form_name: string
  form_status: string
  form_description: string
  is_active: boolean
  created_at: string
}

interface Ticket {
  request_id: string
  Status: number
  asset_id?: string
  username: string
  work?: string
  detail_work?: string
  problem_description?: string
  type_of_work?: string
  formType?: string
  created_at: string
  updated_at?: string
}

interface Asset {
  site: string
  category: string
}

export default function ReportPage() {
  const router = useRouter()
  const { user, logout, isLoading } = useAuth()
  const [feedbacks, setFeedbacks] = useState<Feedback[]>([])
  const [tickets, setTickets] = useState<Ticket[]>([])
  const [assets, setAssets] = useState<Asset[]>([])
  const [loading, setLoading] = useState(true)
  
  const [feedbackStats, setFeedbackStats] = useState({
    total: 0,
    excellent: 0,
    good: 0,
    fair: 0,
    poor: 0,
    bad: 0,
    avgRating: 0
  })

  const [ticketStats, setTicketStats] = useState({
    total: 0,
    pending: 0,
    inProgress: 0,
    completed: 0,
    cancelled: 0
  })

  const [siteStats, setSiteStats] = useState<{[key: string]: number}>({})
  const [categoryStats, setCategoryStats] = useState<{[key: string]: number}>({})
  
  // State สำหรับ Dialog
  const [selectedSite, setSelectedSite] = useState<string | null>(null)
  const [siteDetailStats, setSiteDetailStats] = useState<{[key: string]: number}>({})
  const [isDialogOpen, setIsDialogOpen] = useState(false)

  // State สำหรับ DataTable
  const [searchQuery, setSearchQuery] = useState("")
  const [currentPage, setCurrentPage] = useState(1)
  const [itemsPerPage, setItemsPerPage] = useState(10)
  const [statusFilter, setStatusFilter] = useState<string>("all")

  const handleLogout = async () => {
    await logout()
    router.push('/')
  }

  const handleSiteClick = async (siteName: string) => {
    setSelectedSite(siteName)
    setIsDialogOpen(true)
    
    // ดึงข้อมูลทรัพย์สินของสาขานั้นๆ และแยกตามประเภท
    const siteAssets = assets.filter(asset => asset.site === siteName)
    const categoryCount: {[key: string]: number} = {}
    siteAssets.forEach((asset: Asset) => {
      if (asset.category) {
        categoryCount[asset.category] = (categoryCount[asset.category] || 0) + 1
      }
    })
    setSiteDetailStats(categoryCount)
  }

  useEffect(() => {
    if (isLoading) return
    
    if (!user) {
      router.push('/')
      return
    }
    if (user.role !== 'admin') {
      router.push('/dashboard')
      return
    }
  }, [user, router, isLoading])

  useEffect(() => {
    fetchAllData()
  }, [])

  const fetchAllData = async () => {
    try {
      await Promise.all([
        fetchFeedbacks(),
        fetchTickets(),
        fetchAssets()
      ])
    } catch (error) {
      console.error('Failed to fetch data:', error)
    } finally {
      setLoading(false)
    }
  }

  const fetchFeedbacks = async () => {
    try {
      const response = await fetch('/api/feedback')
      if (response.ok) {
        const data = await response.json()
        setFeedbacks(data)
        
        const total = data.length
        const ratings = data.map((f: Feedback) => parseInt(f.form_status))
        const excellent = ratings.filter((r: number) => r === 5).length
        const good = ratings.filter((r: number) => r === 4).length
        const fair = ratings.filter((r: number) => r === 3).length
        const poor = ratings.filter((r: number) => r === 2).length
        const bad = ratings.filter((r: number) => r === 1).length
        const avgRating = total > 0 ? (ratings.reduce((a: number, b: number) => a + b, 0) / total).toFixed(2) : 0
        
        setFeedbackStats({ total, excellent, good, fair, poor, bad, avgRating: Number(avgRating) })
      }
    } catch (error) {
      console.error('Failed to fetch feedbacks:', error)
    }
  }

  const fetchTickets = async () => {
    try {
      const response = await fetch('/api/tickets')
      if (response.ok) {
        const data = await response.json()
        setTickets(data)
        
        const total = data.length
        const pending = data.filter((t: Ticket) => t.Status === 1).length
        const inProgress = data.filter((t: Ticket) => t.Status === 4).length
        const completed = data.filter((t: Ticket) => t.Status === 2).length
        const cancelled = data.filter((t: Ticket) => t.Status === 3).length
        
        setTicketStats({ total, pending, inProgress, completed, cancelled })
      }
    } catch (error) {
      console.error('Failed to fetch tickets:', error)
    }
  }

  const fetchAssets = async () => {
    try {
      const response = await fetch('/api/assets')
      if (response.ok) {
        const result = await response.json()
        console.log('Assets API response:', result)
        
        // API ส่งกลับมาเป็น { success: true, data: [...] }
        const data = result.success ? result.data : []
        setAssets(data)
        
        console.log('Total assets:', data.length)
        
        const siteCount: {[key: string]: number} = {}
        data.forEach((asset: Asset) => {
          if (asset.site) {
            siteCount[asset.site] = (siteCount[asset.site] || 0) + 1
          }
        })
        setSiteStats(siteCount)
        console.log('Site stats:', siteCount)
        
        const categoryCount: {[key: string]: number} = {}
        data.forEach((asset: Asset) => {
          if (asset.category) {
            categoryCount[asset.category] = (categoryCount[asset.category] || 0) + 1
          }
        })
        setCategoryStats(categoryCount)
        console.log('Category stats:', categoryCount)
      }
    } catch (error) {
      console.error('Failed to fetch assets:', error)
    }
  }

  const getRatingLabel = (rating: string) => {
    const labels: { [key: string]: string } = {
      '5': 'พึงพอใจมากที่สุด',
      '4': 'พึงพอใจมาก',
      '3': 'พึงพอใจปานกลาง',
      '2': 'พึงพอใจน้อย',
      '1': 'ไม่พึงพอใจ'
    }
    return labels[rating] || 'ไม่ระบุ'
  }

  const getRatingBadge = (rating: string) => {
    const styles: { [key: string]: string } = {
      '5': 'bg-green-100 text-green-800 border-green-300',
      '4': 'bg-blue-100 text-blue-800 border-blue-300',
      '3': 'bg-yellow-100 text-yellow-800 border-yellow-300',
      '2': 'bg-orange-100 text-orange-800 border-orange-300',
      '1': 'bg-red-100 text-red-800 border-red-300'
    }
    return styles[rating] || 'bg-gray-100 text-gray-800 border-gray-300'
  }

  // Filter และ Pagination สำหรับ tickets
  const filteredTickets = tickets.filter(ticket => {
    const matchesSearch = 
      ticket.request_id.toLowerCase().includes(searchQuery.toLowerCase()) ||
      ticket.username.toLowerCase().includes(searchQuery.toLowerCase()) ||
      (ticket.work || '').toLowerCase().includes(searchQuery.toLowerCase()) ||
      (ticket.problem_description || '').toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesStatus = statusFilter === "all" || ticket.Status.toString() === statusFilter
    
    return matchesSearch && matchesStatus
  })

  const totalPages = Math.ceil(filteredTickets.length / itemsPerPage)
  const startIndex = (currentPage - 1) * itemsPerPage
  const endIndex = startIndex + itemsPerPage
  const currentTickets = filteredTickets.slice(startIndex, endIndex)

  const getStatusBadge = (status: number) => {
    switch(status) {
      case 1:
        return <Badge className="bg-orange-100 text-orange-800 border-orange-300">รอดำเนินการ</Badge>
      case 4:
        return <Badge className="bg-blue-100 text-blue-800 border-blue-300">กำลังดำเนินการ</Badge>
      case 2:
        return <Badge className="bg-green-100 text-green-800 border-green-300">เสร็จสิ้น</Badge>
      case 3:
        return <Badge className="bg-red-100 text-red-800 border-red-300">ยกเลิก</Badge>
      default:
        return <Badge variant="outline">ไม่ทราบสถานะ</Badge>
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50">
        <header className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
                <ArrowLeft className="h-5 w-5" />
              </Button>
              <div>
                <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
                <p className="text-sm text-muted-foreground">รายงานการประเมิน</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              <div className="text-right">
                <p className="font-medium text-sm">{user?.name || user?.username}</p>
                <p className="text-xs text-muted-foreground capitalize">{user?.role}</p>
              </div>
              <Button variant="outline" onClick={handleLogout}>Logout</Button>
            </div>
          </div>
        </header>
        <div className="container mx-auto p-6">
          <div className="flex items-center justify-center h-[calc(100vh-200px)]">
            <div className="text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
              <p className="text-muted-foreground">กำลังโหลดข้อมูล...</p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <div>
              <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
              <p className="text-sm text-muted-foreground">รายงานการประเมิน</p>
            </div>
          </div>
          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="font-medium text-sm">{user?.name || user?.username}</p>
              <p className="text-xs text-muted-foreground capitalize">{user?.role}</p>
            </div>
            <Button variant="outline" onClick={handleLogout}>Logout</Button>
          </div>
        </div>
      </header>

      <div className="container mx-auto p-6 space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">รายงานสถิติระบบบำรุงรักษา</h1>
            <p className="text-muted-foreground mt-1">สรุปภาพรวมการทำงานของระบบ</p>
          </div>
        </div>

        <Tabs defaultValue="tickets" className="w-full">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="tickets" className="flex items-center gap-2">
              <Wrench className="h-4 w-4" />
              การแจ้งซ่อม
            </TabsTrigger>
            <TabsTrigger value="sites" className="flex items-center gap-2">
              <Building2 className="h-4 w-4" />
              ทรัพย์สินตามสาขา
            </TabsTrigger>
            <TabsTrigger value="categories" className="flex items-center gap-2">
              <Package className="h-4 w-4" />
              ประเภททรัพย์สิน
            </TabsTrigger>
            <TabsTrigger value="feedback" className="flex items-center gap-2">
              <Star className="h-4 w-4" />
              ความพึงพอใจ
            </TabsTrigger>
          </TabsList>

          {/* Tab 1: สถิติการแจ้งซ่อม */}
          <TabsContent value="tickets" className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-5 gap-4">
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">พึงหมด</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold">{ticketStats.total}</div>
                  <p className="text-xs text-muted-foreground mt-1">รายการ</p>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">รอดำเนินการ</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-orange-600">{ticketStats.pending}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {ticketStats.total > 0 ? ((ticketStats.pending / ticketStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">กำลังดำเนินการ</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-blue-600">{ticketStats.inProgress}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {ticketStats.total > 0 ? ((ticketStats.inProgress / ticketStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">เสร็จสิ้น</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-green-600">{ticketStats.completed}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {ticketStats.total > 0 ? ((ticketStats.completed / ticketStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">ยกเลิก</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-red-600">{ticketStats.cancelled}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {ticketStats.total > 0 ? ((ticketStats.cancelled / ticketStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>การกระจายของสถานะงาน</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {[
                    { label: 'รอดำเนินการ', count: ticketStats.pending, color: 'bg-orange-500' },
                    { label: 'กำลังดำเนินการ', count: ticketStats.inProgress, color: 'bg-blue-500' },
                    { label: 'เสร็จสิ้น', count: ticketStats.completed, color: 'bg-green-500' },
                    { label: 'ยกเลิก', count: ticketStats.cancelled, color: 'bg-red-500' }
                  ].map((item) => {
                    const percentage = ticketStats.total > 0 ? (item.count / ticketStats.total) * 100 : 0
                    return (
                      <div key={item.label} className="space-y-2">
                        <div className="flex items-center justify-between text-sm">
                          <div className="flex items-center gap-2">
                            <span className="font-medium">{item.label}</span>
                            <span className="text-muted-foreground">({item.count})</span>
                          </div>
                          <span className="font-medium">{percentage.toFixed(1)}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2.5">
                          <div className={`${item.color} h-2.5 rounded-full`} style={{ width: `${percentage}%` }}></div>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>ประวัติการแจ้งซ่อมทั้งหมด</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-[100px]">ลำดับ</TableHead>
                        <TableHead>เลขที่คำขอ</TableHead>
                        <TableHead>ผู้แจ้ง</TableHead>
                        <TableHead>รายการ</TableHead>
                        <TableHead>ประเภท</TableHead>
                        <TableHead>สถานะ</TableHead>
                        <TableHead className="text-right">วันที่แจ้ง</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {tickets.length === 0 ? (
                        <TableRow>
                          <TableCell colSpan={7} className="text-center text-muted-foreground py-8">
                            ยังไม่มีข้อมูลการแจ้งซ่อม
                          </TableCell>
                        </TableRow>
                      ) : (
                        tickets.map((ticket, index) => (
                          <TableRow key={ticket.request_id}>
                            <TableCell className="font-medium">{index + 1}</TableCell>
                            <TableCell className="font-mono">{ticket.request_id}</TableCell>
                            <TableCell>{ticket.username}</TableCell>
                            <TableCell className="max-w-xs truncate">
                              {ticket.work || ticket.problem_description || '-'}
                            </TableCell>
                            <TableCell>
                              <Badge variant="outline">
                                {ticket.formType === 'request' ? 'ขออุปกรณ์' : 'แจ้งซ่อม'}
                              </Badge>
                            </TableCell>
                            <TableCell>
                              {ticket.Status === 1 && (
                                <Badge className="bg-orange-100 text-orange-800 border-orange-300">
                                  รอดำเนินการ
                                </Badge>
                              )}
                              {ticket.Status === 4 && (
                                <Badge className="bg-blue-100 text-blue-800 border-blue-300">
                                  กำลังดำเนินการ
                                </Badge>
                              )}
                              {ticket.Status === 2 && (
                                <Badge className="bg-green-100 text-green-800 border-green-300">
                                  เสร็จสิ้น
                                </Badge>
                              )}
                              {ticket.Status === 3 && (
                                <Badge className="bg-red-100 text-red-800 border-red-300">
                                  ยกเลิก
                                </Badge>
                              )}
                            </TableCell>
                            <TableCell className="text-right text-sm">
                              {new Date(ticket.created_at).toLocaleDateString('th-TH', {
                                year: 'numeric',
                                month: 'short',
                                day: 'numeric',
                                hour: '2-digit',
                                minute: '2-digit'
                              })}
                            </TableCell>
                          </TableRow>
                        ))
                      )}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Tab 2: สถิติทรัพย์สินตามสาขา */}
          <TabsContent value="sites" className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {Object.entries(siteStats).sort((a, b) => b[1] - a[1]).map(([site, count]) => {
                const total = Object.values(siteStats).reduce((a, b) => a + b, 0)
                const percentage = total > 0 ? (count / total) * 100 : 0
                return (
                  <Card 
                    key={site} 
                    className="cursor-pointer hover:shadow-lg transition-shadow hover:border-blue-500"
                    onClick={() => handleSiteClick(site)}
                  >
                    <CardHeader className="pb-3">
                      <div className="flex items-center justify-between">
                        <Building2 className="h-5 w-5 text-blue-500" />
                        <Badge variant="secondary">{percentage.toFixed(1)}%</Badge>
                      </div>
                      <CardTitle className="text-lg mt-2">{site || 'ไม่ระบุสาขา'}</CardTitle>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-2">
                        <div className="flex items-center justify-between">
                          <span className="text-sm text-muted-foreground">จำนวนทรัพย์สิน</span>
                          <span className="text-2xl font-bold text-blue-600">{count}</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2">
                          <div className="bg-blue-500 h-2 rounded-full" style={{ width: `${percentage}%` }}></div>
                        </div>
                        <p className="text-xs text-muted-foreground text-center mt-2">
                          คลิกเพื่อดูรายละเอียด
                        </p>
                      </div>
                    </CardContent>
                  </Card>
                )
              })}
              {Object.keys(siteStats).length === 0 && (
                <div className="col-span-full text-center text-muted-foreground py-8">
                  ยังไม่มีข้อมูลทรัพย์สิน
                </div>
              )}
            </div>
          </TabsContent>

          {/* Tab 3: สถิติประเภททรัพย์สิน */}
          <TabsContent value="categories" className="space-y-4">
            <Card>
              <CardHeader>
                <CardTitle>จำนวนทรัพย์สินแยกตามประเภท</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {Object.entries(categoryStats).sort((a, b) => b[1] - a[1]).map(([category, count]) => {
                    const total = Object.values(categoryStats).reduce((a, b) => a + b, 0)
                    const percentage = total > 0 ? (count / total) * 100 : 0
                    return (
                      <div key={category} className="space-y-2">
                        <div className="flex items-center justify-between text-sm">
                          <div className="flex items-center gap-2">
                            <span className="font-medium">{category || 'ไม่ระบุประเภท'}</span>
                            <span className="text-muted-foreground">({count} รายการ)</span>
                          </div>
                          <span className="font-medium">{percentage.toFixed(1)}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2.5">
                          <div className="bg-purple-500 h-2.5 rounded-full" style={{ width: `${percentage}%` }}></div>
                        </div>
                      </div>
                    )
                  })}
                  {Object.keys(categoryStats).length === 0 && (
                    <div className="text-center text-muted-foreground py-8">
                      ยังไม่มีข้อมูลทรัพย์สิน
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Tab 4: รายงานผลการประเมินความพึงพอใจ */}
          <TabsContent value="feedback" className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">จำนวนการประเมินพึงหมด</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold">{feedbackStats.total}</div>
                  <p className="text-xs text-muted-foreground mt-1">รายการ</p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">คะแนนเฉลี่ย</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-blue-600">{feedbackStats.avgRating}</div>
                  <p className="text-xs text-muted-foreground mt-1">จาก 5 คะแนน</p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">พึงพอใจมากที่สุด</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-green-600">{feedbackStats.excellent}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {feedbackStats.total > 0 ? ((feedbackStats.excellent / feedbackStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">ไม่พึงพอใจ</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-red-600">{feedbackStats.bad}</div>
                  <p className="text-xs text-muted-foreground mt-1">
                    {feedbackStats.total > 0 ? ((feedbackStats.bad / feedbackStats.total) * 100).toFixed(1) : 0}%
                  </p>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>การกระจายของคะแนนประเมิน</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {[
                    { rating: 5, label: 'พึงพอใจมากที่สุด', count: feedbackStats.excellent, color: 'bg-green-500' },
                    { rating: 4, label: 'พึงพอใจมาก', count: feedbackStats.good, color: 'bg-blue-500' },
                    { rating: 3, label: 'พึงพอใจปานกลาง', count: feedbackStats.fair, color: 'bg-yellow-500' },
                    { rating: 2, label: 'พึงพอใจน้อย', count: feedbackStats.poor, color: 'bg-orange-500' },
                    { rating: 1, label: 'ไม่พึงพอใจ', count: feedbackStats.bad, color: 'bg-red-500' }
                  ].map((item) => {
                    const percentage = feedbackStats.total > 0 ? (item.count / feedbackStats.total) * 100 : 0
                    return (
                      <div key={item.rating} className="space-y-2">
                        <div className="flex items-center justify-between text-sm">
                          <div className="flex items-center gap-2">
                            <span className="font-medium">{item.label}</span>
                            <span className="text-muted-foreground">({item.count})</span>
                          </div>
                          <span className="font-medium">{percentage.toFixed(1)}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2.5">
                          <div className={`${item.color} h-2.5 rounded-full`} style={{ width: `${percentage}%` }}></div>
                        </div>
                      </div>
                    )
                  })}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>รายละเอียดการประเมินพึงหมด</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-[100px]">ลำดับ</TableHead>
                        <TableHead>เลขที่คำขอ</TableHead>
                        <TableHead>ระดับความพึงพอใจ</TableHead>
                        <TableHead>คะแนน</TableHead>
                        <TableHead>หมายเหตุ</TableHead>
                        <TableHead className="text-right">วันที่ประเมิน</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {feedbacks.length === 0 ? (
                        <TableRow>
                          <TableCell colSpan={6} className="text-center text-muted-foreground py-8">
                            ยังไม่มีข้อมูลการประเมิน
                          </TableCell>
                        </TableRow>
                      ) : (
                        feedbacks.map((feedback, index) => (
                          <TableRow key={feedback.form_id}>
                            <TableCell className="font-medium">{index + 1}</TableCell>
                            <TableCell className="font-mono">{feedback.form_name}</TableCell>
                            <TableCell>
                              <Badge className={getRatingBadge(feedback.form_status)}>
                                {getRatingLabel(feedback.form_status)}
                              </Badge>
                            </TableCell>
                            <TableCell>
                              <div className="flex gap-0.5">
                                {[1, 2, 3, 4, 5].map((star) => (
                                  <span
                                    key={star}
                                    className={`text-lg ${
                                      parseInt(feedback.form_status) >= star
                                        ? 'text-yellow-400'
                                        : 'text-gray-300'
                                    }`}
                                  >
                                    ★
                                  </span>
                                ))}
                              </div>
                            </TableCell>
                            <TableCell className="max-w-xs truncate">
                              {feedback.form_description || '-'}
                            </TableCell>
                            <TableCell className="text-right text-sm">
                              {new Date(feedback.created_at).toLocaleDateString('th-TH', {
                                year: 'numeric',
                                month: 'short',
                                day: 'numeric',
                                hour: '2-digit',
                                minute: '2-digit'
                              })}
                            </TableCell>
                          </TableRow>
                        ))
                      )}
                    </TableBody>
                  </Table>
                </div>
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>

      {/* Dialog แสดงรายละเอียดทรัพย์สินของสาขา */}
      <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
        <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2">
              <Building2 className="h-5 w-5 text-blue-500" />
              รายละเอียดทรัพย์สิน - {selectedSite}
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 mt-4">
            <div className="grid grid-cols-2 gap-4">
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">ทรัพย์สินทั้งหมด</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-blue-600">
                    {selectedSite ? siteStats[selectedSite] : 0}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1">รายการ</p>
                </CardContent>
              </Card>
              <Card>
                <CardHeader className="pb-3">
                  <CardTitle className="text-sm font-medium text-muted-foreground">ประเภททรัพย์สิน</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="text-3xl font-bold text-purple-600">
                    {Object.keys(siteDetailStats).length}
                  </div>
                  <p className="text-xs text-muted-foreground mt-1">ประเภท</p>
                </CardContent>
              </Card>
            </div>

            <Card>
              <CardHeader>
                <CardTitle>แยกตามประเภททรัพย์สิน</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {Object.entries(siteDetailStats).sort((a, b) => b[1] - a[1]).map(([category, count]) => {
                    const total = Object.values(siteDetailStats).reduce((a, b) => a + b, 0)
                    const percentage = total > 0 ? (count / total) * 100 : 0
                    return (
                      <div key={category} className="space-y-2">
                        <div className="flex items-center justify-between text-sm">
                          <div className="flex items-center gap-2">
                            <Package className="h-4 w-4 text-purple-500" />
                            <span className="font-medium">{category || 'ไม่ระบุประเภท'}</span>
                            <span className="text-muted-foreground">({count} รายการ)</span>
                          </div>
                          <span className="font-medium">{percentage.toFixed(1)}%</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2.5">
                          <div 
                            className="bg-gradient-to-r from-purple-500 to-blue-500 h-2.5 rounded-full transition-all" 
                            style={{ width: `${percentage}%` }}
                          ></div>
                        </div>
                      </div>
                    )
                  })}
                  {Object.keys(siteDetailStats).length === 0 && (
                    <div className="text-center text-muted-foreground py-8">
                      ไม่มีข้อมูลประเภททรัพย์สิน
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  )
}
